*
{
    color:white;
}
.title{
    font-family: "Yeseva One", serif;
    font-weight: 400;
    font-size:35px;
    color:#c6de41;
}
.buttons{
    list-style: none;
    text-decoration: none;
    font-family: "Montserrat", sans-serif;
    font-weight:600;
    font-size:20px;
    color:#ede2e2;
    display: flex;
    
    width: 64%;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
    transition-duration:0.3sec;
}


.buttons :hover{
    color: #c6de41;
}
.nav{
    display:flex;
    justify-content: space-between;
    align-items: center;
    
}
.sign_in{
    background-color: #6c822e;
    padding:10px 35px;
    border-radius:52px;
  }
 .nav .greencolor{
    color:#c6de41;
    padding-bottom: 2px;
    border-bottom:solid 2px #c6de41;
 }