.Extend_total{
    display:flex;
    padding-top:10%;
}


.Extend_h2{
    width:45%;
    font-size:38px;
    color: #c6de41;
}

.Extend_Side_text{
    width:55%
}

.Extend_sub_text_title{
    font-size: 20px;
    text-align: center;
}

.Extend_sub_sub_text{
    font-size:18px;
    text-align: center;
    margin-top:10px;
}

.Extend_total .Extend_Side_text .Extend_division .Extend_leftpart img {
    width:100px;
    height:100px;
    display: block;
    margin: auto;
    
}

.Extend_total .Extend_Side_text .Extend_division .Extend_rightpart img {
    width:100px;
    height:100px;
    display: block;
    margin: auto;
}

.Extend_division{
    display:flex;
    justify-content: center;
}

.Extend_leftpart{
    align-items: center;
}

.Upperpart{
    margin-bottom: 30px;
    margin-top:0px;
    opacity:100%;
}