.Menu-Name{
    color :white;
    opacity: 100%;
    font-family: "Montserrat", sans-serif;
    font-weight:600;
    font-size: 30px;
    margin :0px;
}
.Menu-rate{
    color:white;
    opacity:100%
}

.menu-list{
    border: solid 2px white;
    width:42%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding:20px;
    margin: 2%;
}

.main-menu .menu-list img{
    width:165px;
    height:110px;
    padding: 3px 5px;
}
.category-complete{
    display:flex;
    padding:0px;
    padding :10px 0px 15px;
}
.category{
    margin:0px;
    font-size: 20px;
    padding-right:5px
}
.Menu-offer{
    font-family: "Montserrat", sans-serif;
    font-weight:500;
    font-size: 15px;
    margin :0px;
    width:80px;
}
.Menu-rate{
    font-family: "Montserrat", sans-serif;
    font-weight:600;
    font-size: 30px;
    width:60px;
    margin :0px;
}
.menu-bottom{
    display: flex;
    justify-content: center;
    align-items: center;
}
.zero{
    padding:0px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.main-menu{
    display: flex;
    justify-content: center;
    align-items: center;
    width:95%;
    height: auto;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0px 2.5%;
}

.main-menu .menu-list .menu-text img{
    height:15px;
    width:auto;
    padding: 4.5px;
}